import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Tab from "@mui/material/Tab";

const dispatchedEvents = [];

const SrsMuiTab = function (props, ref) {
  return (
    <Tab
      label={props.label}
      value={props.value}
      disabled={props.disabled}
      icon={props.icon}
      wrapped={props.wrapped}
      onClick={props.onClick}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiTab component */
`;

const props = [
  {
    name: "label",
    defaultValue: "",
    type: "string",
  },
  {
    name: "value",
    defaultValue: "",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "icon",
    defaultValue: null,
    type: "object",
  },
  {
    name: "wrapped",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiTab),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
